.collectionCard {
  color: white;
  background-color: #1a1c1e;
  border-radius: 3rem;
  overflow: hidden;
  width: 20rem;
  height: 40rem;
  margin-right: 5rem;
}

.details {
  padding: 2rem;
}

.name {
  font-size: 2rem;
  font-weight: 2rem;
}

.id {
  color: #a1a5b0;
  font-size: 1.5rem;
  font-weight: 2rem;
}
.collectionCard > img {
  width: 100%;
}

.priceContainer {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  object-fit: contain;
}
.price {
  margin-left: 0rem;
  font-weight: 2rem;
  font-size: 1.5rem;
}
