.organicList {
  cursor: pointer;
  display: flex;
  margin-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: 0.1rem solid white;
  overflow: scroll;
}
.organicList ::-webkit-scrollbar {
  display: hidden;
}

@media screen and (max-width: 450px) {
  .organicList {
    overflow: scroll;
  }
  .organicList ::-webkit-scrollbar {
    display: hidden;
  }
}
