* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
}

.body {
  background: linear-gradient(90deg, green, white);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 300px;
}
.signinContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1rem solid darkgreen;
  padding: 1rem;
  box-shadow: 0px 10px 0x 0px rgba(0, 0, 0, 0.1);
  background: linear-gradient(200deg, green, white);
}

.signin {
  font-size: 5rem;
  padding: 3rem;
}

.username {
  font-size: 2rem;
}

.password {
  font-size: 2rem;
}

.submit {
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.signinButton {
  padding: 0.5rem;
  cursor: pointer;
  box-sizing: border-box;
  line-height: 1.5em;
  border: 1px solid #6a802c;
  background: linear-gradient(180deg, #b5cc02 0%, #6a802c 100%);
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
}

.registerButton {
  padding: 0.5rem;
  cursor: pointer;
  box-sizing: border-box;
  line-height: 1.5em;
  border: 1px solid #6a802c;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
  background: linear-gradient(180deg, #b5cc02 0%, #6a802c 100%);
  border-radius: 50%;
}
