.app {
  background-color: green;
  padding: 2rem;
  min-height: 100vh;
  min-width: 100vw;
}

@media screen and (max-width: 450px) {
  .app {
    background: green no-repeat center center fixed;
    -moz-background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    padding: 2rem;
    overflow-x: scroll;
    overflow-y: scroll;
  }
}
