.main {
  max-height: 50vh;
}

.mainContent {
  display: flex;
  padding-bottom: 1rem;
  border-bottom: 0.1rem solid white;
}

.organicHighlight {
  flex: 0.25;
  display: flex;
  align-items: center;
  justify-content: center;
}

.organicContainer {
  border-radius: 1rem;
  overflow: hidden;
  display: flex;
  margin-right: 2rem;
  height: 20rem;
  object-fit: contain;
}

.title {
  font-size: 5rem;
  font-weight: 8rem;
}

.organicDetails > div {
  margin: 0 1 rem;
}

.JoeOnTheGrow {
  object-fit: contain;
  max-width: min(25vw, 30vh);
  max-height: 30vw;
}

.organicDetails {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0.75;
}

.ownerLink {
  border-radius: 1rem;
  overflow: hidden;
  display: flex;
  margin-top: 10rem;
  height: 10rem;
  object-fit: contain;
}
