.header {
  color: white;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.header > div {
  margin: 1rem;
}
.imgLogo {
  width: 5rem;
  border-radius: 3rem;
}

.searchLogo {
  width: 1.5rem;
  display: flex;
}

.searchBar {
  background-color: #cbff7b;
  height: 3.5rem;
  flex: 1;
  border-radius: 3rem;
  display: flex;
  align-items: center;
}

.searchIconContainer {
  margin: 1rem;
}

.searchInput {
  background-color: transparent;
  border: none;
  outline: none;
  color: darkgreen;
  font-size: 1rem;
  width: 100%;
  margin-left: 5%;
}

.headerItems {
  display: flex;
  color: rgb(248, 250, 228);
}
.headerItems > p {
  margin: 1rem;
}

.headerActions {
  display: flex;
  align-items: center;
}

.headerActions > div {
  margin: 1rem;
}

.themeSwitchContainer {
  display: flex;
  background-color: #cbff7b;
  border-radius: 3rem;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

@media screen and (max-width: 450px) {
  .searchBar {
    background-color: #cbff7b;
    height: 3.5rem;
    flex: 1;
    min-width: 100vw;
    display: flex;
    border-radius: 3rem;
    align-items: center;
  }
  .logoContainer {
    justify-content: center;
    align-items: center;
    object-fit: contain;
  }
}

.themeSwitchContainer > img {
  height: 2rem;
}

.loginButton {
  background: linear-gradient(to right, green, lightgreen);
  padding: 1rem 3rem;
  border-radius: 3rem;
  color: black;
  font-weight: bold;
}
